import {Model, Status} from './model';
import {Entity, Field} from '../decorators/decorators';

@Entity()
export class Invoice extends Model {
  @Field()
  orderNumber?: string;
  @Field()
  orderAmount?: number;
  @Field()
  debtAmount?: number;
  @Field()
  additionalAmount?: number;
  @Field()
  totalAmount?: number;
  @Field()
  paidAmount?: number;
  @Field()
  billingType?: string;
  @Field()
  currency?: string;
  @Field()
  exchangeRate?: string;
  @Field()
  bankName?: string;
  @Field()
  status?: Status.TypeEnum;

  constructor(obj?: object) {
    super(obj);
  }
}

