import {Injectable} from '@angular/core';
import {MatDialog} from '@angular/material';
import {AlertDialog} from '../components/alert-dialog/alert.dialog';
import {ConfirmDialog} from '../components/confirm-dialog/confirm.dialog';
import {TakePhotoDialog} from '../components/take-photo-dialog/take-photo.dialog';
import {CropPhotoDialog} from '../components/crop-photo-dialog/crop-photo.dialog';

@Injectable({
  providedIn: 'root'
})
export class DialogService {

  constructor(private dialog: MatDialog) {
  }

  // @ts-ignore
  public openAlert(title: string, message: string): Promise<void> {
    return new Promise<void>(resolve => {
      this.dialog.open(AlertDialog, {
        width: '300px',
        data: {title, message}
      }).afterClosed().subscribe(() => {
        resolve();
      });
    });
  }

  public openConfirm(title: string, message: string): Promise<boolean> {
    return new Promise<boolean>(resolve => {
      this.dialog.open(ConfirmDialog, {
        width: '300px',
        data: {title, message}
      }).afterClosed().subscribe(result => {
        resolve(!!result);
      });
    });
  }

  public openCaptureDialog(): Promise<{ file: File | Blob, dataUrl: string, base64String: string | ArrayBuffer }> {
    return new Promise<{ file: File | Blob, dataUrl: string, base64String: string | ArrayBuffer }>(resolve => {
      this.dialog.open(TakePhotoDialog, {
        // width: '400px',
        // data: {title, message}
      }).afterClosed().subscribe(result => {
        resolve(result);
      });
    });
  }

  public openCropDialog(dataUrl: string): Promise<{ file: File | Blob, dataUrl: string, base64String: string | ArrayBuffer }> {
    return new Promise<{ file: File | Blob, dataUrl: string, base64String: string | ArrayBuffer }>(resolve => {
      this.dialog.open(CropPhotoDialog, {
        // width: '400px',
        data: {dataUrl}
      }).afterClosed().subscribe(result => {
        resolve(result);
      });
    });
  }




}
