import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';
import {HomeComponent} from './pages/home/home.component';
import {AuthGuard} from './sub-modules/auth/guards/auth.guard';
import {TestComponentsComponent} from "./pages/test-components/test-components.component";

const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full'
  },
  {
    path: 'auth',
    loadChildren: () => import('./sub-modules/auth/auth.module').then(m => m.AuthModule),
  },
  // { path: 'secure', component: SecureComponent, pathMatch: 'full' },
  {
    path: 'home',
    component: HomeComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'test-components',
    component: TestComponentsComponent,
    canActivate: [AuthGuard]
  },
  {
    path: 'accounts',
    loadChildren: () => import('./sub-modules/accounts/accounts.module').then(m => m.AccountsModule),
    canActivate: [AuthGuard]
  },
  {
    path: 'billings',
    loadChildren: () => import('./sub-modules/billings/billings.module').then(m => m.BillingsModule),
    canActivate: [AuthGuard]
  },
  {
    path: 'appointments',
    loadChildren: () => import('./sub-modules/appointments/appointments.module').then(m => m.AppointmentsModule),
    canActivate: [AuthGuard]
  },
  {
    path: 'debts',
    loadChildren: () => import('./sub-modules/debts/debts.module').then(m => m.DebtsModule),
    canActivate: [AuthGuard]
  },
  {
    path: 'reports',
    loadChildren: () => import('./sub-modules/reports/reports.module').then(m => m.ReportsModule),
    canActivate: [AuthGuard]
  },
  {
    path: 'storage',
    loadChildren: () => import('./sub-modules/storage/storage.module').then(m => m.StorageModule),
    canActivate: [AuthGuard]
  },
  {
    path: 'tasks',
    loadChildren: () => import('./sub-modules/tasks/tasks.module').then(m => m.TasksModule),
    canActivate: [AuthGuard]
  },
  {
    path: 'equipments',
    loadChildren: () => import('./sub-modules/equipments/equipments.module').then(m => m.EquipmentsModule),
    canActivate: [AuthGuard]
  },
  // otherwise redirect to home
  {path: '**', redirectTo: ''}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule {
}
