import {BrowserModule} from '@angular/platform-browser';
import {APP_INITIALIZER, Injector, NgModule} from '@angular/core';

import {AppRoutingModule} from './app-routing.module';
import {AppContainer} from './app.container';
import {HttpClient, HttpClientModule} from '@angular/common/http';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core';
import {MaterialModule} from './material-module';
import {HomeComponent} from './pages/home/home.component';
import {IcInjectorService, IcCoreModule} from './sub-modules/ic-core';
import {CommonModule} from '@angular/common';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {AmplifyAngularModule, AmplifyService} from 'aws-amplify-angular';
import {DateAdapter, ErrorStateMatcher, MAT_DATE_FORMATS, MAT_DATE_LOCALE, ShowOnDirtyErrorStateMatcher} from '@angular/material';
import {MomentDateAdapter} from '@angular/material-moment-adapter';
import {InitializerProvider} from './sub-modules/ic-core';
import jQuery from 'jquery';
import { TestComponentsComponent } from './pages/test-components/test-components.component';

export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}

export const MY_FORMATS = {
  parse: {
    dateInput: 'DD/MM/YYYY',
  },
  display: {
    dateInput: 'DD/MM/YYYY',
    monthYearLabel: 'MM YYYY',
    dateA11yLabel: 'DD/MM/YYYY',
    monthYearA11yLabel: 'MM YYYY',
  },
};


export function initializerProviderFactory(initializer: InitializerProvider) {
  return () => initializer.load();
}

jQuery(document).on('focusout', 'input', (event) => {
  const val = jQuery(event.target).val();
  if (typeof val === 'string' || val instanceof String) {
    jQuery(event.target).val(val.trim());
  }
  jQuery(event.target).val((jQuery(event.target).val()));
});


@NgModule({
  declarations: [
    AppContainer,
    HomeComponent,
    TestComponentsComponent,
  ],
  imports: [
    CommonModule,
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,

    AppRoutingModule,

    MaterialModule,

    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    }),
    IcCoreModule.forRoot(),
    AmplifyAngularModule
  ],
  providers: [
    InitializerProvider,
    { provide: APP_INITIALIZER, useFactory: initializerProviderFactory, deps: [InitializerProvider], multi: true },
    AmplifyService,
    { provide: MAT_DATE_LOCALE, useValue: 'vi' },
    { provide: DateAdapter, useClass: MomentDateAdapter, deps: [MAT_DATE_LOCALE] },
    { provide: MAT_DATE_FORMATS, useValue: MY_FORMATS },
    // {provide: ErrorStateMatcher, useClass: ShowOnDirtyErrorStateMatcher}
    ],
  bootstrap: [AppContainer]
})
export class AppModule {
  constructor(injector: Injector) {
    IcInjectorService.setAppInjector(injector);
  }
}
