import {Model, Status} from './model';
import {Entity, Field} from '../decorators/decorators';

@Entity()
export class Payment extends Model {
  @Field()
  billingNumber?: string;
  @Field()
  customerNumber?: string;
  @Field()
  paidAmount?: number;
  @Field()
  billingType?: string;
  @Field()
  status?: PaymentStatus.TypeEnum;

  constructor(obj?: object) {
    super(obj);
  }
}

// tslint:disable-next-line:no-namespace
export namespace PaymentStatus {
  export type TypeEnum = 'done' | 'processing' | 'failed';
  export const TypeEnum = {
    Done: 'done' as TypeEnum,
    Processing: 'processing' as TypeEnum,
    Failed: 'failed' as TypeEnum
  };
}


