import {Model} from './model';
import {Entity, Field} from '../decorators/decorators';

@Entity()
export class OrderedItem extends Model {
  @Field()
  type?: string;
  @Field()
  code?: string;
  @Field()
  unit?: string;
  @Field()
  unitPrice?: number;
  @Field()
  compensationAmount?: number;
  @Field()
  quantity?: number;
  @Field()
  discountPercentage?: number;
  constructor(obj?: object) {
    super(obj);
  }
}

