import {Component, Input, OnInit} from '@angular/core';

@Component({
  selector: 'ic-abstract-page',
  templateUrl: './abstract-page.component.html',
  styleUrls: ['./abstract-page.component.scss']
})
export class AbstractPageComponent {
  pageTitle: string;
  breadcrumb: Array<{ link?: string, title: string }>;

  constructor(pageTitle: string, breadcrumb: Array<{ link?: string, title: string }>) {
    this.pageTitle = pageTitle;
    this.breadcrumb = breadcrumb;
  }

  public async canDeactivate(): Promise<boolean> {
    return true;
  }
}
