import {EventEmitter, Injectable} from '@angular/core';

// @ts-ignore
import PouchDB from 'pouchdb';
// @ts-ignore
import PouchFind from 'pouchdb-find';
import {Model} from '../models/model';
import {UtilityService} from '../../ic-core/services/utility.service';
import {T} from '@angular/cdk/keycodes';

PouchDB.plugin(PouchFind);

@Injectable({
  providedIn: 'root'
})
// tslint:disable-next-line:no-shadowed-variable
export class AbstractDao<T extends Model> {

  private databaseName: string;
  private modelClass: new(obj: object) => T;
  // @ts-ignore
  private db: PouchDB.Database;
  // private sync: any;

  private changeListener: EventEmitter<any> = new EventEmitter<any>();

  constructor(clazz: new(obj: object) => T) {
    this.modelClass = clazz;
    this.databaseName =
      clazz.name.replace(/[A-Z]/g, (match, offset, string) => {
        return (offset ? '_' : '') + match.toLowerCase();
      });
    console.log('init data: %s', this.databaseName);

    // PouchDB.debug.enable('*');
    this.db = new PouchDB(this.databaseName);
    // this.createDBIndex();

    /*
    this.sync = PouchDB.sync(this.databaseName, 'http://localhost:5984/' + this.databaseName, {
      live: true,
      retry: true
    }).on('change', (data) => {
      console.log('%s change: %s', clazz.name, JSON.stringify(data));
      this.changeListener.emit(data);
      // handle change
    }).on('paused', (err) => {
      // replication paused (e.g. replication up to date, user went offline)
      console.log('replicate paused: %s', JSON.stringify(err));
    }).on('active', () => {
      // replicate resumed (e.g. new changes replicating, user went back online)
      console.log('replicate active');
    }).on('denied', (err) => {
      // a document failed to replicate (e.g. due to permissions)
      console.log('replicate denied: %s', JSON.stringify(err));
    }).on('complete', (info) => {
      // handle complete
      console.log('replicate complete: %s', JSON.stringify(info));
    }).on('error', (err) => {
      // handle error
      console.log('replicate error: %s', JSON.stringify(err));
    });
    */
    this.getInfo().then((details) => {
      if (details.doc_count === 0 && details.update_seq === 0) {
        this.postInit();
      } else {
        console.log('dao exists');
      }
    }).catch((err) => {
      console.log('error: ' + err);
      return;
    });
  }

  private async createDBIndex() {
    const fields = UtilityService.findDeclaredProperties(this.modelClass);
    const fieldNames = fields.map(field => field.name);
    await this.db.createIndex({
      index: {fields: fieldNames}
    });
  }

  // create default data
  protected postInit() {
  }

  public getChangeListener() {
    return this.changeListener;
  }

  async getInfo() {
    const info = await this.db.info();
    console.log(info);
    return info;
  }

  // tslint:disable-next-line:variable-name
  async get(id: string) {

    try {
      const doc = await this.db.get(id);
      return new this.modelClass(doc);
    } catch (err) {
      console.trace(err.message);
      return null;
    }
  }

  async getAll(): Promise<T[]> {
    let result;
    try {
      result = await this.db.allDocs({
        include_docs: true,
        attachments: true
      });
    } catch (err) {
      console.trace(err.message);
    }
    return UtilityService.parseDataToModel(result.docs, this.modelClass);
  }

  async search(query: object): Promise<T[]> {
    let result;
    try {
      result = await this.db.find({selector: query});
    } catch (err) {
      console.trace(err.message);
    }
    return (!!result) ? result.docs.map(item => new this.modelClass(item)) : [];
  }

  async create(doc: T) {
    let result = null;
    if (!doc.id) {
      try {
        result = await this.db.post(doc); // Create a new document and let PouchDB auto-generate an _id for it.
        /*
        Example Response:
        {
          "ok" : true,
          "id" : "8A2C3761-FFD5-4770-9B8C-38C33CED300A",
          "rev" : "1-d3a8e0e5aa7c8fff0c376dac2d8a4007"
        }
        */
        doc.id = result.id;
      } catch (err) {
        console.trace(err.message);
        throw err;
      }
    } else {
      result = await this.replace(doc); // Create a new doc with an _id of 'mydoc'
    }

    return doc;
  }

  // tslint:disable-next-line:variable-name
  async replace(obj: T) {
    let result;
    try {
      if (!obj._id) {
        obj._id = obj.id;
      }
      // @ts-ignore
      // if (obj._rev) {
      // @ts-ignore
      // obj['_rev'] = undefined; // destroy 'conflict' version when put
      // obj['_rev_tree'] = undefined;
      // }
      result = await this.db.put(obj); // Create a new doc with an _id of 'mydoc'
    } catch (err) {
      console.trace(err.message);
      throw err;
    }
    return obj;
  }

  // tslint:disable-next-line:variable-name
  async update(obj: T) {
    let doc = await this.get(obj.id);
    if (!doc) {
      return this.create(obj);
    } else {
      doc = Object.assign(doc, obj);
      return this.replace(doc);
      // doc = new this.modelClass(JSON.parse(JSON.stringify(doc)));
    }
  }

  // tslint:disable-next-line:variable-name
  async delete(_id) {
    try {
      const doc = await this.db.get(_id);
      const result = await this.db.remove(doc._id, doc._rev);
      return result;
    } catch (err) {
      if (err.reason === 'missing') {// there is no _id in dao
        return {};
      } else {
        console.trace(err.message);
        throw err;
      }
    }
  }
}
