import {AfterViewInit, Component, ViewChild} from '@angular/core';
import {TranslateService} from '@ngx-translate/core';
import {NavigationCancel, NavigationEnd, NavigationError, NavigationStart, Router} from '@angular/router';
import {PageLoaderService} from './sub-modules/ic-core';

@Component({
  selector: 'ic-root',
  templateUrl: './app.container.html',
  styleUrls: ['./app.container.scss']
})
// tslint:disable-next-line:component-class-suffix
export class AppContainer implements AfterViewInit {

  constructor(private translate: TranslateService, private router: Router, private pageLoaderService: PageLoaderService) {
    translate.setDefaultLang('en');
  }

  ngAfterViewInit(): void {
    this.pageLoaderService.hidePageLoader();
  }

  public switchLanguage(language: string) {
    this.translate.use(language);
  }

  public isAuthenticationUrl() {
    return this.router.url.startsWith('/auth');
  }


}
