import {Component, ElementRef, EventEmitter, OnInit, Output, ViewChild} from '@angular/core';
import {DialogService} from '../../services/dialog.service';


const base64ToBlob = (dataURI, contentType) => {
  const byteString = atob(dataURI.split(',')[1]);
  const ab = new ArrayBuffer(byteString.length);
  const ia = new Uint8Array(ab);
  for (let i = 0; i < byteString.length; i++) {
    ia[i] = byteString.charCodeAt(i);
  }
  return new Blob([ab], {type: contentType});
};
const blobToBase64 = blob => new Promise((resolve, reject) => {
  const reader = new FileReader();
  reader.onerror = reject;
  reader.onload = () => {
    resolve(reader.result);
  };
  reader.readAsDataURL(blob);
});

@Component({
  selector: 'ic-choose-photo',
  templateUrl: './choose-photo.component.html',
  styleUrls: ['./choose-photo.component.scss']
})
export class ChoosePhotoComponent implements OnInit {

  @ViewChild('input', {static: false}) input: ElementRef;
  @Output() chose = new EventEmitter<{ file: File | Blob, dataUrl: string, base64String: string | ArrayBuffer }>();

  constructor(private dialogService: DialogService) { }

  ngOnInit() {
  }

  chooseFile() {
    this.input.nativeElement.click();

  }

  readPhoto(event) {
    const input = event.target;
    const url = input.value;
    const ext = url.substring(url.lastIndexOf('.') + 1).toLowerCase();
    if (input.files && input.files[0] && (ext === 'gif' || ext === 'png' || ext === 'jpeg' || ext === 'jpg')) { // check again
      const file = input.files[0];
      const reader = new FileReader();
      const dataUrl = URL.createObjectURL(file);
      const contentType = input.files[0].type;
      reader.onload = async (ev) => {
        // @ts-ignore
        const base64String = ev.target.result;
        // this.chose.emit({file, dataUrl, base64String});
        await this.cropPhoto({file, dataUrl, base64String});
      };
      reader.readAsDataURL(file);
    }
  }

  async cropPhoto(data: { file: File | Blob, dataUrl: string, base64String: string | ArrayBuffer }){
    const croppedData = await this.dialogService.openCropDialog(data.dataUrl);
    this.chose.emit(croppedData);
  }

}
