  public static findSuper(child: new(...args: any[]) => void): new(...args: any[]) => void {
    // tslint:disable-next-line:variable-name
    const _super: new(...args: any[]) => void = Object.getPrototypeOf(child.prototype).constructor;
    return _super;
  }

  public static findDeclaredProperties(clazz: new(...args: any[]) => void)
    : Array<{ name: string, type: new(...args: any[]) => void, mapping: string, itemType: new(...args: any[]) => void }> {
    // tslint:disable-next-line:variable-name
    let _clazz = clazz;
    let properties: Array<{ name: string, type: new(...args: any[]) => void, mapping: string, itemType: new(...args: any[]) => void }> = [];
    while (_clazz.name && _clazz.name !== 'Object') {
      const p = (Reflect.getOwnMetadata(DESIGN_META_DATA.ENTITY, _clazz) || []).filter(
        item => !properties.some(i => i.name === item.name)// remove item is existed in child class
      );
      properties = [...p, ...properties];
      _clazz = this.findSuper(_clazz);
    }
    return properties;
  }

  public static collectSchema(clazz: new(...args: any[]) => void):
    Array<{ name: string, properties: Array<{ name: string, type: string }> }> {
    // tslint:disable-next-line:variable-name
    const _clazz: new(...args: any[]) => void = clazz;
    let schema: Array<{ name: string, properties: Array<{ name: string, type: string }> }> = [];
    const declaredProperties: Array<{ name: string, type: new(...args: any[]) => void }> = this.findDeclaredProperties(_clazz);
    let properties: Array<{ name: string, type: string }>;
    if (declaredProperties.length > 0) {
      properties = declaredProperties.map(property => {
        const childSchema = this.collectSchema(property.type);
        if (childSchema.length > 0) {
          schema.push(...childSchema);
        }
        return {name: property.name, type: property.type.name};
      });
      schema.push({name: clazz.name, properties});
      schema = schema.filter((item, index, target) => target.indexOf(item) === index);
    }
    return schema;
  }

  public static getTypeProperty(clazz: new(...args: any[]) => void, propertyName: string) {
    // tslint:disable-next-line:variable-name
    let _clazz = clazz;
    const propertyNameItems = propertyName.split('.');
    propertyNameItems.forEach(propertyNameItem => {
      if (_clazz) {
        const declaredProperties = this.findDeclaredProperties(_clazz);
        const declaredProperty = declaredProperties.find(property => property.name === propertyNameItem);
        if (declaredProperty) {
          _clazz = declaredProperty.type;
        } else {
          _clazz = undefined;
        }
      }
    });
    return _clazz;
  }

  public static isNotEmptyObject(obj) {
    let result = false;
    if (obj !== null && obj !== undefined) {
      if (['string', 'number', 'boolean'].includes(typeof obj)) {
        result = true;
      } else {
        if (typeof obj === 'object') {
          if (obj instanceof String || obj instanceof Date || obj instanceof Number || obj instanceof Boolean) {
            result = true;
          } else if (Object.keys(obj).length > 0) {
            Object.keys(obj).forEach(property => {
              if (!result) {
                result = this.isNotEmptyObject(obj[property]);
              }
            });
          }
        }
      }
    }
    return result;
  }

